.navigation {
  &__nav {
    font-size: 2rem;
    height: 7rem;
    background: $color-secondary; /* fallback for old browsers */
    background: -webkit-linear-gradient(
      to right,
      $color-primary,
      $color-secondary
    ); /* Chrome 10-25, Safari 5.1-6 */
    background: linear-gradient(
      to right,
      $color-primary,
      $color-secondary
    ); /* W3C, IE 10+/ Edge, Firefox 16+, Chrome 26+, Opera 12+, Safari 7+ */
    box-shadow: 0 0.5rem 1.5rem rgba(0, 0, 0, 0.2);
  }

  &__img {
    height: 3rem;
    margin-right: 1rem;
  }
  &__list {
    display: flex;
    align-items: center;
    height: 100%;
    justify-content: space-between;
    list-style: none;
  }
  &__item {
    padding: 0 2rem;
    &--user {
      display: flex;
      align-items: center;
    }
  }
  &__link {
    color: #fff;

    text-decoration: none;
  }
}
